function sliderConstruction() {
    var swiper = new Swiper('.construction .mySwiper', {
        slidesPerView: 1,
        spaceBetween: 15,
        effect: "fade",
        navigation: {
            nextEl: ".construction .swiper-button-next",
            prevEl: ".construction .swiper-button-prev",
        },
    })
}

$(document).ready(function () {
  sliderConstruction();

    // Accordion
    $(".item-show").click((function(){
        $(this).toggleClass("item-show--active"),
        $(this).siblings(".item-hidden").slideToggle()
    }));


    //Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        // $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })

    function closeModal() {
        $('.modal--active video').each(function () {
            this.pause();
            this.currentTime = 0;
        });

        $('.modal').removeClass("modal--active")
        // $("body").removeClass("fixed-body")
    }

})

    // Telephone-mask
    document.addEventListener("DOMContentLoaded", function() {
            const input = document.querySelector("#phone");
            if (!input) {
                return;
            }
            const iti = window.intlTelInput(input, {
                separateDialCode: true,
                utilsScript: "https://cdn.jsdelivr.net/npm/intl-tel-input@17.0.8/build/js/utils.js",
                initialCountry: "ru",
                nationalMode: false,
            });

            if(iti && iti.on) {
                iti.on("countrychange", function() {
                    const currentNumber = input.value;
                    input.value = "";
                    input.value = currentNumber;
                });
            } else {
        }
    });